.input-form input {
  text-align: center;
  outline: none;
  border: none;
  border-bottom: 1px solid #fff;
  background-color: transparent;
  color: #fff;
  font-size: 1.5rem;
  font-family: "Poppins", sans-serif;
  padding: 0.2rem 0;
  width: 100%;
}
.input-form button {
  outline: none;
  border: none;
  background-color: #fff;
  color: #000;
  font-size: 1.5rem;
  font-family: "Poppins", sans-serif;
  padding: 0.5rem 0;
  margin: 0.5rem 0;
  width: 80%;
  border-radius: 5px;
  cursor: pointer;
}
.input-form button:hover {
  background-color: #000;
  color: #fff;
  transition: 1s;
}
